<template>
  <b-tabs>
    <b-tab-item label="List Elements">
      <div class="columns is-multiline is-mobile">
        <div
          class="column is-2-desktop is-6-mobile"
          v-for="element in nonGrowthXPElements"
          :key="element.id"
        >
          <Element :element="element"></Element>
        </div>
      </div>
    </b-tab-item>
    <b-tab-item label="GrowthXP elements">
      <div class="columns is-multiline is-mobile">
        <div
          class="column is-2-desktop is-6-mobile"
          v-for="element in growthXPElements"
          :key="element.id"
        >
          <Element :element="element"></Element>
        </div>
      </div>
    </b-tab-item>
    <b-tab-item label="Strains">
      <div class="columns is-multiline is-mobile">
        <div
          class="column is-6-desktop is-12-mobile"
          v-for="strain in strains"
          :key="strain.id"
        >
          <Strain :strain="strain"></Strain>
        </div>
      </div>
    </b-tab-item>
  </b-tabs>
</template>
<script>
import Element from "@/components/Element";
import Strain from "@/components/Strain";
import contract from "./../Utils/Contract.js";

export default {
  components: { Element, Strain },
  data() {
    return {};
  },
  computed: {
    strains: function() {
      return contract.strains;
    },
    elements: function() {
      return contract.elements;
    },
    growthXPElements: function() {
      return contract.elements.filter(element => element.isGrowthXP);
    },
    nonGrowthXPElements: function() {
      return contract.elements.filter(element => !element.isGrowthXP);
    }
  }
};
</script>
