<template>
  <div class="plant">
    <div v-if="showListGrowthXP" class="growthxp-list">
      <GrowthXPChoose :plantId="plant.id" @onClosed="onGrowthXPClosedHanlder()" />
    </div>
    <div class="plant-overview">
      <div class="id-overlay">{{plant.id}}</div>
      <div class>Start time: {{formatTime(plant.startTime)}}</div>
      <div class>
        Bonus time: {{plant.bonusTime}} min
        <div
          v-if="showGrowthXP"
          class="button is-success is-small"
          @click="showListGrowthXP=!showListGrowthXP"
        >Speed up growth</div>
      </div>
      <div v-if="plant.harvestTime == 0">
        Growth Time Complete: {{(minuteRestToHarvest)}} min
        <div
          v-if="showHarvestButton"
          class="button is-danger is-small"
          @click="harvest()"
        >Harvest now!</div>
      </div>
      <div v-else>Harvested time: {{formatTime(plant.harvestTime)}}</div>
    </div>
    <Strain
      class="plant-strain"
      :strain="strains[plant.strainId]"
      :mulSeed="plant.mulSeed"
      hidePlant
    />
  </div>
</template>

<script>
import Strain from "@/components/Strain.vue";
import contract from "./../Utils/Contract";
import GrowthXPChoose from "@/components/GrowthXPChoose";

import { formatTime } from "@/Utils/Format";
import sleep from "./../Utils/Sleep";

export default {
  data() {
    return {
      minuteRestToHarvest: 0,
      showListGrowthXP: false
    };
  },
  computed: {
    timeToHarvest: function() {
      let startTime = new Date(this.plant.startTime * 1000);
      let timeToHarvest = new Date(this.plant.startTime * 1000);
      timeToHarvest.setMinutes(
        startTime.getMinutes() +
          this.strains[this.plant.strainId].timeToGrown -
          this.plant.bonusTime
      );
      return timeToHarvest;
    },
    showHarvestButton: function() {
      return this.minuteRestToHarvest <= 0;
    },
    showGrowthXP: function() {
      return this.minuteRestToHarvest > 0;
    },
    strains: function() {
      return contract.strains;
    },
    elements: function() {
      return contract.elements;
    }
  },
  components: {
    Strain,
    GrowthXPChoose
  },
  methods: {
    formatTime: function(val) {
      return formatTime(val);
    },
    harvest: async function() {
      await contract.harvest(this.plant.id);
      this.$toast.open({
        message: "Harvest success!",
        type: "is-success"
      });
      await sleep(3000);
      this.$emit("onChanged", this.plant.id);
    },
    onGrowthXPClosedHanlder: async function() {
      this.showListGrowthXP = false;
     await sleep(3000);

      this.$emit("onChanged", this.plant.id);
    }
  },
  created() {
    let self = this;
    let reCalTime = function() {
      self.minuteRestToHarvest = Math.round(
        (self.timeToHarvest - new Date(Date.now())) / (1000 * 60)
      );
    };
    reCalTime();
    setInterval(reCalTime, 60000);
  },
  props: {
    plant: {
      type: Object,
      default: function() {
        return {
          startTime: Date.now(),
          bonusTime: 0,
          harvestTime: 0,
          mulSeed: 1,
          strainId: 0
        };
      }
    }
  }
};
</script>

<style>
.plan {
  padding: 10px;
  border-radius: 10px;
  position: relative;
  box-shadow: rgba(31, 42, 75, 0.2) 0px 20px 40px;
}
.plant-strain {
  box-shadow: 0px 5px 10px rgba(31, 42, 75, 0.2) inset;
  margin-top: 10px;
}
.plant-overview {
  position: relative;
}
.id-overlay {
  font-weight: bold;
  font-size: 100px;
  position: absolute;
  z-index: 0;
  opacity: 0.1;
  right: 0;
  top: -30px;
}
.growthxp-list {
  margin: -10px;
  width: 100%;
  position: absolute;
  z-index: 999;
  background: #fff;
  height: 100%;
}
</style>