<template>
  <b-tabs>
    <b-tab-item label="My Plants" icon="leaf">
      <b-tabs position="is-centered" >
        <b-tab-item label="Growing" icon="flower-tulip">
          <div class="columns is-multiline is-mobile" v-if="loadPlant">
            <div
              class="column is-6-desktop is-12-mobile"
              v-for="plant in myPlantsGrowing()"
              :key="'g'+plant.id" 
            >
              <Plant :plant="plant" @onChanged="plantChanged"></Plant>
            </div>
          </div>
        </b-tab-item>
        <b-tab-item label="Harvestable" icon="rice">
          <div class="columns is-multiline is-mobile" v-if="loadPlant">
            <div
              class="column is-6-desktop is-12-mobile"
              v-for="plant in myPlantsHarvestable()"
              :key="'h'+plant.id"
            >
              <Plant :plant="plant" @onChanged="plantChanged"></Plant>
            </div>
          </div>
        </b-tab-item>
        <b-tab-item label="Completed" icon="check-all">
          <div class="columns is-multiline is-mobile" v-if="loadPlant">
            <div
              class="column is-6-desktop is-12-mobile"
              v-for="plant in myPlantsCompleted()"
              :key="'c'+plant.id"
            >
              <Plant :plant="plant" @onChanged="plantChanged"></Plant>
            </div>
          </div>
        </b-tab-item>
      </b-tabs>
    </b-tab-item>

    <b-tab-item label="My Elements" icon="chemical-weapon">
      <div class="my-element-control columns is-centered is-vcentered">
        <b-loading class="loading" :is-full-page="false" :active.sync="isLoadingBalance"></b-loading>

        <div class="column is-one-quarter">
          <b-button @click="reloadBalance" icon-left="refresh">Reload my balance</b-button>
        </div>
        <div class="column is-one-quarter">
          <b-checkbox v-model="showBlank">Show blank balance</b-checkbox>
        </div>
      </div>

      <div class="columns is-multiline is-variable is-1 is-mobile" v-if="showBalance">
        <div class="column is-2-desktop is-6-mobile" v-for="element in elements()" :key="element.id">
          <Element :element="element" isMyElement :showBlank="showBlank"></Element>
        </div>
      </div>
    </b-tab-item>
  </b-tabs>
</template>

<script>
import Element from "@/components/Element";
import Plant from "@/components/Plant.vue";
import contract from "./../Utils/Contract.js";

export default {
  components: { Element, Plant },
  data() {
    return {
      showBlank: true,
      plantFilter: "harvestable",
      plantKey: 1,
      loadPlant: false,
      showBalance: false,
      isLoadingBalance: false,
    };
  },
  async mounted() {
    this.reloadBalance();
    await contract.loadNewPlants(this.$store.state.myAddress);
    this.loadPlant = true;
  },
  computed: {
    
  },
  methods: {
    myPlantsGrowing: function() {
      let self = this;
      let result = contract.plants.filter(
        plant => plant.harvestTime == 0 && !self.isHarvestable(plant)
      );
      return result;
    },
    myPlantsHarvestable: function() {
      let self = this;
      let result = contract.plants.filter(
        plant => plant.harvestTime == 0 && self.isHarvestable(plant)
      );

      return result;
    },
    myPlantsCompleted: function() {
      let result = contract.plants.filter(plant => plant.harvestTime != 0);
      return result;
    },
    elements: function() {
      return contract.elements;
    },
    reloadBalance: async function() {
      this.isLoadingBalance = true;
      await contract.loadBalances(this.$store.state.myAddress);
      this.showBalance = false;
      await this.$nextTick();
      this.showBalance = true;
      this.isLoadingBalance = false;
    },
    plantChanged: async function(plantId) {
      await contract.loadPlant(plantId,this.$store.state.myAddress);
      this.loadPlant = false;
      await this.$nextTick();
      this.loadPlant = true;
    },
    isHarvestable: function(plant) {
      let startTime = new Date(plant.startTime * 1000);
      let timeToHarvest = new Date(plant.startTime * 1000);
      timeToHarvest.setMinutes(
        startTime.getMinutes() +
          contract.strains[plant.strainId].timeToGrown -
          plant.bonusTime
      );
      return timeToHarvest < new Date(Date.now());
    }
  }
};
</script>

<style>
.my-element-control {
  margin: 10px;
  position: relative;
}
</style>
